import React, { useState, useEffect } from 'react';
import axios from 'axios';
const mongoose = require('mongoose');

const SlotCreate = () => {
  const [venues, setVenues] = useState([]);
  const [selectedVenue, setSelectedVenue] = useState('');
  const [nets, setNets] = useState([]);
  const [selectedNet, setSelectedNet] = useState('');
  const [slotTime, setSlotTime] = useState('');
  const [rate, setRate] = useState('');


  useEffect(() => {
    const fetchVenues = async () => {
      try {
        const response = await axios.get('https://backend013-production.up.railway.app/api/venue');
        console.log(response.data)
        setVenues(response.data);
      } catch (error) {
        console.error(error);
      }
    };
    fetchVenues();
  }, []);

  useEffect(() => {
    const fetchNets = async () => {
      if (selectedVenue) {
        try {
          const response = await axios.get(`https://backend013-production.up.railway.app/api/net/venue/${selectedVenue}`);
          setNets(response.data);
        } catch (error) {
          console.error(error);
        }
      } else {
        setNets([]);
      }
    };
    fetchNets();
  }, [selectedVenue]);

  const handleVenueChange = (e) => {
    setSelectedVenue(e.target.value);
    setSelectedNet('');
  };

  const handleNetChange = (e) => {
    setSelectedNet(e.target.value);
  };

  const handleSlotTimeChange = (e) => {
    setSlotTime(e.target.value);
  };

  const handleSubmit = async (e) => {
    console.log({slotTime,selectedNet,rate})
    e.preventDefault();
    console.log(selectedNet)
    try {
      const response = await axios.post(`https://backend013-production.up.railway.app/api/slot/net/${selectedNet}`, {
        Slot_time:slotTime,
        net_id: selectedNet,
        rate:rate,
        
      });
      console.log(response.data);
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <div className="py-6 mx-96">
      <h2 className="text-lg font-medium mb-2">Create a slot</h2>
      <form onSubmit={handleSubmit} className="flex flex-col space-y-4">
        <div className="flex flex-col">
          <label htmlFor="venue" className="mb-1 font-medium">
            Select a venue:
          </label>
          <select
            id="venue"
            value={selectedVenue}
            onChange={handleVenueChange}
            className="border border-gray-300 p-2 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-400"
          >
            <option value="">--Please choose a venue--</option>
            {venues.map((venue) => (
              <option key={venue._id} value={venue._id}>
                {venue.name}
              </option>
            ))}
          </select>
        </div>

        {selectedVenue && (
          <div className="flex flex-col">
            <label htmlFor="net" className="mb-1 font-medium">
              Select a net:
            </label>
            <select
              id="net"
              value={selectedNet}
              onChange={handleNetChange}
              className="border border-gray-300 p-2 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-400"
            >
              <option value="">--Please choose a net--</option>
              {nets.map((net) => (
                <option key={net._id} value={net._id}>
                  {net.net_number}
                </option>
              ))}
            </select>
          </div>
        )}

        {selectedNet && (
          <div className="flex flex-col">
            <label htmlFor="slotTime" className="mb-1 font-medium">
              Select a slot time:
            </label>
            <input
              type="text"
              id="slotTime"
              value={slotTime}
              onChange={handleSlotTimeChange}
              className="border border-gray-300 p-2 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-400"
            />
             <label htmlFor="rate" className="mb-1 font-medium">
              Rate:
            </label>
            <input
              type="text"
              id="rate"
              value={rate}
              onChange={(e) => setRate(e.target.value)}
              className="border border-gray-300 p-2 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-400"
            />
            <button type="submit" className="bg-blue-500 text-white rounded-md py-2 px-4 mt-4 hover:bg-blue-600">
              Create Slot
            </button>
           

          </div>
        )}
      </form>
    </div>

  );
};

export default SlotCreate;
