import { Route, Routes } from "react-router-dom";

import Home from "./page/Home";
import About from "./page/About";
import Contact from "./page/Contact";
import Dashboard from "./page/dashboard/Dashboard";
import Header from "./component/Header";
import HeaderAndSidebar from "./component/HeaderAndSidebar";
import VenueDelete from "./page/dashboard/Entry";
import NetsCreate from "./page/dashboard/NetsCreate";
import NetEdit from "./page/dashboard/NetCreate";
import NetDelete from "./page/dashboard/NetDelete";
import SlotCreate from "./page/dashboard/SlotsCreate";
import VenueUpdate from "./page/dashboard/VenueUpdate";
import SlotFormDelete from "./page/dashboard/SlotsDelete";
import SlotUpdate from "./page/dashboard/Slotsupdate";
import SlotBooking from "./page/dashboard/BookingsCreate";
import MultiSlotBooking from "./page/dashboard/BookingMultiSelect";

function App() {
  return (
    <div>
      <Routes>
        <Route path="frontend_013/" element={<Header />}>
          <Route index element={<Home/>}/>
          <Route path="about" element={<About/>} />
          <Route path="contact" element={<Contact/>} />
        </Route>

        <Route path="frontend_013/venue" element={<HeaderAndSidebar/>}>
          <Route index element={<Dashboard/>}/>
          
          <Route path="frontend_013/venue/venue-delete" element={<VenueDelete/>} />
          <Route path="frontend_013/venue/venue-create" element={<Dashboard/>} />
          <Route path="frontend_013/venue/slots-delete" element={<SlotFormDelete/>}/>
           <Route path="frontend_013/venue/nets-create" element={<NetsCreate/>} />
          <Route path="frontend_013/nets-delete" element={<NetDelete/>} />
           <Route path="frontend_013/venue-update" element={<VenueUpdate/>} />
           <Route path="frontend_013/nets-update" element={<NetEdit/>} />
          <Route path="frontend_013/slots-create" element={<SlotCreate/>} />
           <Route path="frontend_013/slots-update" element={<SlotUpdate/> } />
          <Route path="frontend_013/booking-list" element={<SlotBooking/>} />
          <Route path="frontend_013/multi-slot-booking" element={<MultiSlotBooking/>} />
        </Route>
        
        <Route path="*" element={<div><h1 className="text-center text-5xl font-bold">404 Not Found</h1></div>} />
      </Routes>
    </div>
  );
}

export default App;
