import React, { useState, useEffect } from "react";
import axios from "axios";

const VenueDelete = () => {
  const [venues, setVenues] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchVenues = async () => {
      try {
        const response = await axios.get("https://backend013-production.up.railway.app/api/venue");
        console.log(response.data)
        setVenues(response.data);
        setLoading(false);
      } catch (error) {
        console.error(error);
      }
    };
    fetchVenues();
  }, []);

  const deleteVenue = async (id) => {
    try {
      await axios.delete(`https://backend013-production.up.railway.app/api/venue/${id}`);
      setVenues(venues.filter((venue) => venue._id !== id));
    } catch (error) {
      console.error(error);
    }
  };

  return (
<div class="p-6 mx-96">
  <h2 class="text-lg font-medium mb-4">Venue List</h2>
  {loading ? (
    <p>Loading...</p>
  ) : (
    <ul class="space-y-2">
      {venues.map((venue) => (
        <li class="flex items-center p-6 bg-white justify-between" key={venue._id}>
          <span class="flex-grow font-medium">{venue.name}, {venue.address}, {venue.city}, {venue.state}, {venue.country}, {venue.pincode}</span>
          <button class="ml-4 px-4 py-2 bg-red-500 text-white rounded" onClick={() => deleteVenue(venue._id)}>Delete</button>
        </li>
      ))}
    </ul>
  )}
</div>

  );
};

export default VenueDelete;
