import React, { useState, useEffect } from "react";
import axios from "axios";

const Dashboard = () => {
  const [venues, setVenues] = useState([]);
  const [name, setName] = useState("");
  const [address, setAddress] = useState("");
  const [city, setCity] = useState("");
  const [state, setState] = useState("");
  const [country, setCountry] = useState("");
  const [pincode, setPincode] = useState("");





  const addVenue = async (e) => {
    alert(`Venue Created Successfully with Id : ${venues}`)
    e.preventDefault();
    try {
      const response = await axios.post("https://backend013-production.up.railway.app/api/venue", {
        name,
        address,
        city,
        state,
        country,
        pincode,
      });
      console.log(response.data); // add this line
      setVenues([...venues, response.data.data]);
      setName("");
      setAddress("");
      setCity("");
      setState("");
      setCountry("");
      setPincode("");

    } catch (error) {
      console.error(error);
    }
  };



  return (
    <div className="mx-96 bg-white p-6">
    <h2 className="text-2xl font-bold mb-4">Add Venue</h2>
    <form onSubmit={addVenue} className="flex flex-col space-y-4">
      <label htmlFor="name" className="text-lg font-medium">
        Name:
      </label>
      <input
        type="text"
        id="name"
        value={name}
        onChange={(e) => setName(e.target.value)}
        className="border border-gray-400 rounded-lg py-2 px-4 focus:outline-none focus:border-blue-500"
      />
      <label htmlFor="address" className="text-lg font-medium">
        Address:
      </label>
      <input
        type="text"
        id="address"
        value={address}
        onChange={(e) => setAddress(e.target.value)}
        className="border border-gray-400 rounded-lg py-2 px-4 focus:outline-none focus:border-blue-500"
      />
      <label htmlFor="city" className="text-lg font-medium">
        City:
      </label>
      <input
        type="text"
        id="city"
        value={city}
        onChange={(e) => setCity(e.target.value)}
        className="border border-gray-400 rounded-lg py-2 px-4 focus:outline-none focus:border-blue-500"
      />
      <label htmlFor="state" className="text-lg font-medium">
        State:
      </label>
      <input
        type="text"
        id="state"
        value={state}
        onChange={(e) => setState(e.target.value)}
        className="border border-gray-400 rounded-lg py-2 px-4 focus:outline-none focus:border-blue-500"
      />
      <label htmlFor="country" className="text-lg font-medium">
        Country:
      </label>
      <input
        type="text"
        id="country"
        value={country}
        onChange={(e) => setCountry(e.target.value)}
        className="border border-gray-400 rounded-lg py-2 px-4 focus:outline-none focus:border-blue-500"
      />
      <label htmlFor="pincode" className="text-lg font-medium">
        Pincode:
      </label>
      <input
        type="text"
        id="pincode"
        value={pincode}
        onChange={(e) => setPincode(e.target.value)}
        className="border border-gray-400 rounded-lg py-2 px-4 focus:outline-none focus:border-blue-500"
      />
      <button
        type="submit"
        className="bg-blue-500 text-white py-2 px-4 rounded-lg hover:bg-blue-600 focus:outline-none focus:bg-blue-600"
      >
        Add Venue
      </button>
    </form>
  </div>
  );
}

export default Dashboard;