import React, { useState, useEffect } from 'react';
import axios from 'axios';

const NetEdit = () => {
    const [venues, setVenues] = useState([]);
    const [selectedVenue, setSelectedVenue] = useState('');
    const [nets, setNets] = useState([]);
    const [editNet, setEditNet] = useState(null);
    const [netNumber, setNetNumber] = useState('');

    useEffect(() => {
        const fetchVenues = async () => {
            try {
                const response = await axios.get('https://backend013-production.up.railway.app/api/venue');
                setVenues(response.data);
            } catch (error) {
                console.error(error);
            }
        };
        fetchVenues();
    }, []);

    useEffect(() => {
        const fetchNets = async () => {
            if (selectedVenue) {
                try {
                    const response = await axios.get(`https://backend013-production.up.railway.app/api/net/venue/${selectedVenue}`);
                    setNets(response.data);
                } catch (error) {
                    console.error(error);
                }
            } else {
                setNets([]);
            }
        };
        fetchNets();
    }, [selectedVenue]);

    const handleVenueChange = (e) => {
        setSelectedVenue(e.target.value);
    };

    const handleDelete = async (netId) => {
        try {
            const response = await axios.delete(`https://backend013-production.up.railway.app/api/net/${netId}`);
            console.log(response.data);
            setNets(nets.filter((net) => net._id !== netId));
        } catch (error) {
            console.error(error);
        }
    };

    const handleEdit = (net) => {
        setEditNet(net);
        setNetNumber(net.net_number);
    };

    const handleUpdate = async (e) => {
        e.preventDefault();
        try {
            const response = await axios.patch(`https://backend013-production.up.railway.app/api/net/${editNet._id}`, {
                net_number: netNumber
            });
            console.log(response.data);
            setEditNet(null);
            setNetNumber('');
            setNets(nets.map((net) => (net._id === editNet._id ? { ...net, net_number: netNumber } : net)));
        } catch (error) {
            console.error(error);
        }
    };

    return (
        <div class="py-4 mx-96">
            <label class="block font-bold mb-2" htmlFor="venue">Select a venue:</label>
            <select class="block w-full border rounded py-2 px-3 mb-3" id="venue" value={selectedVenue} onChange={handleVenueChange}>
                <option value="">--Please choose a venue--</option>
                {venues.map((venue) => (
                    <option key={venue._id} value={venue._id}>
                        {venue.name}
                    </option>
                ))}
            </select>

            {selectedVenue && (
                <table class="border-collapse w-full">
                    <thead>
                        <tr class="border-b border-gray-300">
                            <th class="text-left font-bold py-2 px-3">Venue</th>
                            <th class="text-left font-bold py-2 px-3">Net Number</th>
                            <th class="text-center font-bold py-2 px-3">Edit</th>
                            <th class="text-center font-bold py-2 px-3">Delete</th>
                        </tr>
                    </thead>
                    <tbody>
                        {nets.map((net) => (
                            <tr key={net._id} class="border-b border-gray-300">
                                <td class="py-2 px-3">{net.venue_id.name}</td>
                                <td class="py-2 px-3">
                                    {editNet && editNet._id === net._id ? (
                                        <input class="border rounded py-2 px-3" type="text" value={netNumber} onChange={(e) => setNetNumber(e.target.value)} />
                                    ) : (
                                        net.net_number
                                    )}
                                </td>
                                <td class="text-center py-2 px-3">
                                    {editNet && editNet._id === net._id ? (
                                        <div>
                                            <form onSubmit={handleUpdate}>
                                                <label class="block mb-2" htmlFor="netNumber">Net number:</label>
                                                <input class="border rounded py-2 px-3 mb-2 w-full" type="text" id="netNumber" value={netNumber} onChange={(e) => setNetNumber(e.target.value)} />
                                                <button class="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded mr-2" type="submit">Update</button>
                                                <button class="bg-gray-500 hover:bg-gray-700 text-white font-bold py-2 px-4 rounded" type="button" onClick={() => setEditNet(null)}>Cancel</button>
                                            </form>
                                        </div>
                                    ) : (
                                        <div>
                                            <button class="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded mr-2" onClick={() => setEditNet(net)}>Edit</button>
                                            <button class="bg-red-500 hover:bg-red-700 text-white font-bold py-2 px-4 rounded" onClick={() => handleDelete(net._id)}>Delete</button>
                                        </div>
                                    )}
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            )}
        </div>


    )
};


export default NetEdit;

