import { useState, useEffect } from 'react';
import axios from 'axios';
 function MultipleSlotBooking() {
  const [venues, setVenues] = useState([]);
  const [selectedVenue, setSelectedVenue] = useState('');
  const [nets, setNets] = useState([]);
  const [selectedNet, setSelectedNet] = useState('');
  const [slots, setSlots] = useState([]);
  const [selectedSlot, setSelectedSlot] = useState('');
  const [slotTime, setSlotTime] = useState('');
  const [date, setDate] = useState('');
   useEffect(() => {
    const fetchVenues = async () => {
      try {
        const response = await axios.get('http://localhost:5000/api/venue');
        console.log(response.data);
        setVenues(response.data);
      } catch (error) {
        console.error(error);
      }
    };
    fetchVenues();
  }, []);
   useEffect(() => {
    const fetchNets = async () => {
      if (selectedVenue) {
        try {
            const response = await axios.get(`http://localhost:5000/api/net/venue/${selectedVenue}`);
          setNets(response.data);
        } catch (error) {
          console.error(error);
        }
      } else {
        setNets([]);
      }
    };
    fetchNets();
  }, [selectedVenue]);
   useEffect(() => {
    const fetchSlots = async () => {
      if (selectedNet) {
        try {
          const response = await axios.get(
            `http://localhost:5000/api/slot/net/${selectedNet}`
          );
          setSlots(response.data);
        } catch (err) {
          console.log(err);
        }
      } else {
        setSlots([]);
      }
    };
    fetchSlots();
  }, [selectedNet]);
   const handleVenueChange = (e) => {
    setSelectedVenue(e.target.value);
    setSelectedNet('');
  };
   const handleNetChange = async (e) => {
    setSelectedNet(e.target.value);
  };
   const handleSlotChange = async (e) => {
    setSelectedSlot(e.target.value);
  };
   const handleSlotTimeChange = (e) => {
    setSlotTime(e.target.value);
  };
   const handleSubmit = async (e) => {
    e.preventDefault();
    alert(
      `Booking Created successfully for ${selectedSlot} of ${selectedNet} of ${selectedVenue}`
    );
    try {
      await axios.post(`http://localhost:5000/api/bookings`, {
        net_id: selectedNet._id,
        date: date,
      });
       setSelectedVenue('');
      setSelectedNet('');
      setSelectedSlot('');
    } catch (error) {
      console.error(error);
    }
  };
   return (
    <form onSubmit={handleSubmit} className='mx-96 flex '>
      <label htmlFor="venue">Select a venue:</label>
      <select id="venue" name="venue" onChange={handleVenueChange} value={selectedVenue}>
        <option value="">--Select a venue--</option>
        {venues.map((venue) => (
          <option key={venue._id} value={venue._id}>
            {venue.name}
          </option>
        ))}
      </select>
       <label htmlFor="net">Select a net:</label>
      <select id="net" name="net" onChange={handleNetChange} value={selectedNet}>
        <option value="">--Select a net--</option>
        {nets.map((net) => (
          <option key={net._id} value={net._id}>
            {net.name}
          </option>
        ))}
      </select>
       <label htmlFor="slot">Select a slot:</label>
      <select id="slot" name="slot" onChange={handleSlotChange} value={selectedSlot}>
        <option value="">--Select a slot--</option>
        {slots.map((slot) => (
          <option key={slot._id} value={slot._id}>
            {slot.time}
          </option>
        ))}
      </select>
       <label htmlFor="slot-time">Enter the time for the slot:</label>
      <input
        id="slot-time"
        name="slot-time"
        type="text"
        onChange={handleSlotTimeChange}
        value={slotTime}
      />
       <label htmlFor="date">Enter the date for the booking:</label>
      <input id="date" name="date" type="date" onChange={(e) => setDate(e.target.value)} />
       <button type="submit">Book Slot</button>
    </form>
  );
}
 export default MultipleSlotBooking;