import React, { useState, useEffect } from 'react';
import axios from 'axios';

const NetsCreate = () => {
    const [venues, setVenues] = useState([]);
    const [selectedVenue, setSelectedVenue] = useState('');
    const [netNumber, setNetNumber] = useState('');

    useEffect(() => {
        const fetchVenues = async () => {
            try {
                const response = await axios.get('https://backend013-production.up.railway.app/api/venue');
                setVenues(response.data);
            } catch (error) {
                console.error(error);
            }
        };
        fetchVenues();
    }, []);

    const handleSubmit = async (e) => {
        alert("Slot Created Successfully!!")
        e.preventDefault();
        try {
            const response = await axios.post(`https://backend013-production.up.railway.app/api/net/venue/${selectedVenue}`, {
                venueId: selectedVenue,
                net_number:netNumber,
            });
            console.log(response.data);
            // reset form values
            setSelectedVenue('');
            setNetNumber('');
        } catch (error) {
            console.error(error);
        }
    };

    return (
        <form onSubmit={handleSubmit} className="mx-auto max-w-md">
        <div className="mb-4">
          <label htmlFor="venue" className="block font-medium mb-2">
            Venue:
          </label>
          <select
            id="venue"
            className="w-full border rounded py-2 px-3"
            value={selectedVenue}
            onChange={(e) => setSelectedVenue(e.target.value)}
          >
            <option value="">Select a venue</option>
            {venues.map((venue) => (
              <option key={venue._id} value={venue._id}>
                {venue.name}
              </option>
            ))}
          </select>
        </div>
        <div className="mb-4">
          <label htmlFor="netNumber" className="block font-medium mb-2">
            Net number:
          </label>
          <input
            type="text"
            id="netNumber"
            className="w-full border rounded py-2 px-3"
            value={netNumber}
            onChange={(e) => setNetNumber(e.target.value)}
          />
        </div>
        <button
          type="submit"
          className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded"
        >
          Create Net
        </button>
      </form>
      
    );
};

export default NetsCreate;