import React, { useState, useEffect } from 'react';
import axios from 'axios';

const NetDelete = () => {
  const [venues, setVenues] = useState([]);
  const [selectedVenue, setSelectedVenue] = useState('');
  const [nets, setNets] = useState([]);

  useEffect(() => {
    const fetchVenues = async () => {
      try {
        const response = await axios.get('https://backend013-production.up.railway.app/api/venue');
        setVenues(response.data);
      } catch (error) {
        console.error(error);
      }
    };
    fetchVenues();
  }, []);

  useEffect(() => {
    const fetchNets = async () => {
      if (selectedVenue) {
        try {
          const response = await axios.get(`https://backend013-production.up.railway.app/api/net/venue/${selectedVenue}`);
          setNets(response.data);
        } catch (error) {
          console.error(error);
        }
      } else {
        setNets([]);
      }
    };
    fetchNets();
  }, [selectedVenue]);

  const handleVenueChange = (e) => {
    setSelectedVenue(e.target.value);
  };

  const handleDelete = async (netId) => {
    try {
      const response = await axios.delete(`https://backend013-production.up.railway.app/api/net/${netId}`);
      console.log(response.data);
      setNets(nets.filter((net) => net._id !== netId));
    } catch (error) {
      console.error(error);
    }
  };

  return (
<div className='mx-96'>
  <label htmlFor="venue" className="block mb-2">Select a venue:</label>
  <select id="venue" value={selectedVenue} onChange={handleVenueChange} className="block w-full mb-4 p-2 rounded border border-gray-300 focus:border-indigo-500 focus:ring-2 focus:ring-indigo-200">
    <option value="">--Please choose a venue--</option>
    {venues.map((venue) => (
      <option key={venue._id} value={venue._id}>
        {venue.name}
      </option>
    ))}
  </select>

  {selectedVenue && (
    <table className="w-full table-fixed">
      <thead>
        <tr>
          <th className="w-1/2 px-4 py-2">Venue</th>
          <th className="w-1/2 px-4 py-2">Net Number</th>
          <th className="w-1/4 px-4 py-2">Delete</th>
        </tr>
      </thead>
      <tbody>
        {nets.map((net) => (
          <tr key={net._id}>
            <td className="border px-4 py-2">{net.venue_id.name}</td>
            <td className="border px-4 py-2">{net.net_number}</td>
            <td className="border px-4 py-2">
              <button onClick={() => handleDelete(net._id)} className="bg-red-500 hover:bg-red-700 text-white font-bold py-1 px-2 rounded">
                Delete
              </button>
            </td>
          </tr>
        ))}
      </tbody>
    </table>
  )}
</div>

  );
};

export default NetDelete;
