import React, { useState, useEffect } from "react";
import axios from "axios";

const VenueUpdate = () => {
    const [venues, setVenues] = useState([]);
    const [selectedVenue, setSelectedVenue] = useState({});
    const [name, setName] = useState("");
    const [address, setAddress] = useState("");
    const [city, setCity] = useState("");
    const [state, setState] = useState("");
    const [country, setCountry] = useState("");
    const [pincode, setPincode] = useState("");
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const fetchVenues = async () => {
            try {
                const response = await axios.get("https://backend013-production.up.railway.app/api/venue");
                setVenues(response.data);
                setLoading(false);
            } catch (error) {
                console.error(error);
            }
        };
        fetchVenues();
    }, []);

    const handleVenueChange = (e) => {
        const venueId = e.target.value;
        const selected = venues.find((venue) => venue._id === venueId);
        console.log(selected)
        setSelectedVenue(selected);
        setName(selected.name);
        setAddress(selected.address);
        setCity(selected.city);
        setState(selected.state);
        setCountry(selected.country);
        setPincode(selected.pincode);
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            await axios.patch(
                `https://backend013-production.up.railway.app/api/venue/${selectedVenue._id}`,
                {
                    name,
                    address,
                    city,
                    state,
                    country,
                    pincode,
                }
            );
            alert("Venue details updated successfully!");
            setSelectedVenue({});
            setName("");
            setAddress("");
            setCity("");
            setState("");
            setCountry("");
            setPincode("");
        } catch (error) {
            console.error(error);
        }
    };
    return (
<div class="p-6 mx-96">
  <h2 class="text-lg font-medium mb-4">Update Venue</h2>
  {loading ? (
    <p>Loading...</p>
  ) : (
    <form class="space-y-4" onSubmit={handleSubmit}>
      <div>
        <label class="block mb-2 text-lg font-medium" htmlFor="venue">Select Venue:</label>
        <select class="w-full border border-gray-400 py-2 px-3 rounded placeholder-gray-500" id="venue" onChange={handleVenueChange}>
          <option value="none">Select a venue</option>
          {venues.map((venue) => (
          <option key={venue._id} value={venue._id}>{venue.name}</option>
          ))}
        </select>
      </div>
      <div>
        <label class="block mb-2 text-lg font-medium" htmlFor="name">Name:</label>
        <input class="w-full border border-gray-400 py-2 px-3 rounded placeholder-gray-500" type="text" id="name" placeholder="Enter name" value={name} onChange={(e) => setName(e.target.value)} />
      </div>
      <div>
        <label class="block mb-2 text-lg font-medium" htmlFor="address">Address:</label>
        <input class="w-full border border-gray-400 py-2 px-3 rounded placeholder-gray-500" type="text" id="address" placeholder="Enter address" value={address} onChange={(e) => setAddress(e.target.value)} />
      </div>
      <div>
        <label class="block mb-2 text-lg font-medium" htmlFor="city">City:</label>
        <input class="w-full border border-gray-400 py-2 px-3 rounded placeholder-gray-500" type="text" id="city" placeholder="Enter city" value={city} onChange={(e) => setCity(e.target.value)} />
      </div>
      <div>
        <label class="block mb-2 text-lg font-medium" htmlFor="state">State:</label>
        <input class="w-full border border-gray-400 py-2 px-3 rounded placeholder-gray-500" type="text" id="state" placeholder="Enter state" value={state} onChange={(e) => setState(e.target.value)} />
      </div>
      <div>
        <label class="block mb-2 text-lg font-medium" htmlFor="country">Country:</label>
        <input class="w-full border border-gray-400 py-2 px-3 rounded placeholder-gray-500" type="text" id="country" placeholder="Enter country" value={country} onChange={(e) => setCountry(e.target.value)} />
      </div>
      <div>
        <label class="block mb-2 text-lg font-medium" htmlFor="pincode">Pincode:</label>
        <input class="w-full border border-gray-400 py-2 px-3 rounded placeholder-gray-500" type="text" id="pincode" placeholder="Enter pincode" value={pincode} onChange={(e) => setPincode(e.target.value)} />
      </div>
      <button class="px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600" type="submit">Update Venue</button>
    </form>
  )}
</div>

    );
};

export default VenueUpdate;