import React, { useState, useEffect } from 'react';
import axios from 'axios';

const SlotUpdate = () => {
    const [venues, setVenues] = useState([]);
    const [selectedVenue, setSelectedVenue] = useState('');
    const [nets, setNets] = useState([]);
    const [selectedNet, setSelectedNet] = useState('');
    const [slots, setSlots] = useState([]);
    const [selectedSlot, setSelectedSlot] = useState('');
    const [slotTime, setSlotTime] = useState('');
    const [rate, setRate] = useState('');

    useEffect(() => {
        const fetchVenues = async () => {
            try {
                const response = await axios.get('https://backend013-production.up.railway.app/api/venue');
                console.log(response.data)
                setVenues(response.data);
            } catch (error) {
                console.error(error);
            }
        };
        fetchVenues();
    }, []);

    useEffect(() => {
        const fetchNets = async () => {
            if (selectedVenue) {
                try {
                    const response = await axios.get(`https://backend013-production.up.railway.app/api/net/venue/${selectedVenue}`);
                    setNets(response.data);
                } catch (error) {
                    console.error(error);
                }
            } else {
                setNets([]);
            }
        };
        fetchNets();
    }, [selectedVenue]);

    useEffect(() => {
        const fetchSlots = async () => {
            if (selectedNet) {
                try {
                    const response = await axios.get(`https://backend013-production.up.railway.app/api/slot/net/${selectedNet}`);
                    setSlots(response.data);
                }
                catch (err) {
                    console.log(err);
                }
            }
            else {
                setSlots([]);
            }
        };
        fetchSlots();;
    }, [selectedNet])


    const handleVenueChange = (e) => {
        setSelectedVenue(e.target.value);
        setSelectedNet('');
    };

    const handleNetChange = async (e) => {
        setSelectedNet(e.target.value);

    };


    const handleSlotChange = async (e) => {
        setSelectedSlot(e.target.value);
    }

    const handleSlotTimeChange = (e) => {
        setSlotTime(e.target.value);
    };






    const handleSubmit = async (e) => {
        e.preventDefault();
        alert(`Slot Created successfully for ${selectedNet} of ${selectedVenue}`)
        try {
            await axios.patch(
                `https://backend013-production.up.railway.app/api/slot/${selectedSlot}`,
                {
                   net_id:selectedNet._id,
                   Slot_time:slotTime,
                   rate:rate,

                }
            );
            alert("Venue details updated successfully!");
            setSelectedVenue('');
            setSelectedNet('');
            setSelectedSlot('');

            } catch (error) {
            console.error(error);
        }
    };

    return (
        <div className="mx-auto max-w-3xl">
            <h2 className="text-2xl font-bold mb-4">Delete  slot</h2>
            <form onSubmit={handleSubmit}>
                <div className="mb-4">
                    <label htmlFor="venue" className="block font-medium mb-1">Select a venue:</label>
                    <select id="venue" value={selectedVenue} onChange={handleVenueChange} className="w-full border rounded p-2">
                        <option value="">--Please choose a venue--</option>
                        {venues.map((venue) => (
                            <option key={venue._id} value={venue._id}>
                                {venue.name}
                            </option>
                        ))}
                    </select>
                </div>

                {selectedVenue && (
                    <div>
                        <div className="mb-4">
                            <label htmlFor="net" className="block font-medium mb-1">Select a net:</label>
                            <select id="net" value={selectedNet} onChange={handleNetChange} className="w-full border rounded p-2">
                                <option value="">--Please choose a net--</option>
                                {nets.map((net) => (
                                    <option key={net._id} value={net._id}>
                                        {net.net_number}
                                    </option>
                                ))}
                            </select>
                        </div>

                        {selectedNet && slots.length > 0 && (
                            <div>
                                <h3 className="text-lg font-medium mb-2">Slots for Net {selectedNet}</h3>
                                <label htmlFor="net" className="block font-medium mb-1">Select a net:</label>
                                <select id="slot" value={selectedSlot} onChange={handleSlotChange} className="w-full border rounded p-2">
                                    <option value="">--Please choose a Slot--</option>
                                    {slots.map((slot) => (
                                        <option key={slot._id} value={slot._id}>
                                            {slot.Slot_time}
                                        </option>
                                    ))}
                                </select>

                                {selectedSlot && (
                                    <div>
                                        <input
                                            type="text"
                                            id="slotTime"
                                            value={slotTime}
                                            onChange={handleSlotTimeChange}
                                            className="border border-gray-300 p-2 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-400"
                                        />
                                        <label htmlFor="rate" className="mb-1 font-medium">
                                            Rate:
                                        </label>
                                        <input
                                            type="text"
                                            id="rate"
                                            value={rate}
                                            onChange={(e) => setRate(e.target.value)}
                                            className="border border-gray-300 p-2 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-400"
                                        />
                                        <button type="submit" className="bg-blue-500 text-white rounded-md py-2 px-4 mt-4 hover:bg-blue-600">
                                            Update Slot
                                        </button>
                                    </div>
                                )}
                            </div>
                        )}


                    </div>
                )}
            </form>
        </div>

    )
};

export default SlotUpdate;
